<template>
  <div class="container">
    <el-row :gutter="20">
      <div v-show="kafkaMonitorListData && char_groupId!=''">
        <el-col :span="12">
          <div id="spline" style="box-shadow: 0 2px 12px 0 rgba(0,0,0,.1)">
          </div>
        </el-col>
        <el-col :span="12">
          <el-card class="box-card">
            <div slot="header" class="clearfix">
              <span></span>
            </div>
            <div class="text item">
              <el-row>
                <el-col :span="5">
                  
                </el-col>
                <el-col :span="4">
                  {{char_createBy}}
                </el-col>
              </el-row>
            </div>
            <div class="text item">
              <el-row>
                <el-col :span="5">
                  
                </el-col>
                <el-col :span="4">
                  {{char_address}}
                </el-col>
              </el-row>
            </div>
            <div class="text item">
              <el-row>
                <el-col :span="5">
                  topic
                </el-col>
                <el-col :span="4">
                  {{char_topic}}
                </el-col>
              </el-row>
            </div>
            <div class="text item">
              <el-row>
                <el-col :span="5">
                  group
                </el-col>
                <el-col :span="19">
                  {{char_groupId}}
                </el-col>
              </el-row>
            </div>
            <div class="text item">
              <el-row>
                <el-col :span="5">
                  
                </el-col>
                <el-col :span="4">
                  {{char_logSize}}
                </el-col>
              </el-row>
            </div>
            <div class="text item">
              <el-row>
                <el-col :span="5">
                  
                </el-col>
                <el-col :span="4">
                  {{char_offset}}
                </el-col>
              </el-row>
            </div>
            <div class="text item">
              <el-row>
                <el-col :span="5">
                  
                </el-col>
                <el-col :span="4">
                  {{char_lag}}
                </el-col>
              </el-row>
            </div>
            <div class="text item">
              <el-row>
                <el-col :span="5">
                  
                </el-col>
                <el-col :span="19">
                  {{char_des}}
                </el-col>
              </el-row>
            </div>
          </el-card>
        </el-col>
      </div>
    </el-row>

    <AddKafkaMonitorDialog></AddKafkaMonitorDialog>

    <UpdateKafkaMonitorDialog :kafkaMonitor="kafkaMonitorComputed"></UpdateKafkaMonitorDialog>

    <!--    <div v-if="this.$store.state.UpdateKafkaMonitorDialogShow">-->
    <!--      asd-->
    <!--    </div>-->
    <!--<el-button type="primary" @click="showEmailDialog"></el-button>-->
    <!---->
    <AddEmailDialog></AddEmailDialog>

    <!---->
    <el-row :gutter="20" style="margin: 40px 0 0 0">
      <el-col :span="4">
        <el-button type="primary" size="mini" @click="showAddKafkaMonitorDialog"></el-button>
      </el-col>
      <el-col :span="4" :offset="8">
        <el-input
          v-model="searchGroupId"
          suffix-icon="el-icon-search"
          size="mini"
          placeholder="groupId"></el-input>
      </el-col>
      <el-col :span="4">
        <el-input
          v-model="searchCreateBy"
          suffix-icon="el-icon-search"
          size="mini"
          placeholder=""></el-input>
      </el-col>
      <el-col :span="4">
        <el-button
          size="mini"
          type="primary"
          plain
          @click="resetSearchTable">
        </el-button>
      </el-col>
    </el-row>

    <el-table
      :data="kafkaMonitorListPagedData"
      style="width: 100%"
      :row-class-name="tableRowClassName">
      <el-table-column
        prop="topic"
        label="Topic">
      </el-table-column>
      <el-table-column
        prop="groupId"
        label="Group">
      </el-table-column>
      <el-table-column
        width="100"
        prop="lag"
        label="">
      </el-table-column>
      <el-table-column
        prop="address"
        label=""
        width="180">
      </el-table-column>
      <el-table-column
        prop="createBy"
        label=""
        width="100">
      </el-table-column>
      <el-table-column
        label=""
        width="180">
        <template slot-scope="scope">
          <el-tooltip class="item" effect="dark" content="" placement="top-start">
            <el-button
              size="mini"
              type="primary"
              plain
              icon="el-icon-view"
              @click="getGroupAndKafkaMonitorMethod(scope.$index, scope.row)">
            </el-button>
          </el-tooltip>
          <el-tooltip class="item" effect="dark" content="" placement="top-start">
            <el-button
              size="mini"
              type="primary"
              plain
              icon="el-icon-edit"
              @click="handleEdit(scope.$index, scope.row)">
            </el-button>
          </el-tooltip>
          <el-tooltip class="item" effect="dark" content="" placement="top-start">
            <el-button
              size="mini"
              type="danger"
              plain
              icon="el-icon-delete"
              @click="handleDelete(scope.$index, scope.row)">
            </el-button>
          </el-tooltip>
        </template>
      </el-table-column>
    </el-table>
    <div class="block" v-show="kafkaMonitorListPagedData && kafkaMonitorListPagedData.length>0">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[5, 10, 20, 50]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import Highcharts from "highcharts";
  import AddEmailDialog from './AddEmailDialog.vue'
  import AddKafkaMonitorDialog from './AddKafkaMonitorDialog.vue'
  import UpdateKafkaMonitorDialog from './UpdateKafkaMonitorDialog.vue'
  import {
    saveKafkaMonitor,
    getGroupList,
    getGroupOffsetAndLagList,
    getGroupAndKafkaMonitor
  } from '../../../../api/kafkaMonitorApi'

  export default {
    name: "KafkaOffsetMonitor",
    components: {
      AddEmailDialog,
      AddKafkaMonitorDialog,
      UpdateKafkaMonitorDialog
    },
    data() {
      return {
        //kafkakafkaMonitorListPagedData
        kafkaMonitorListData: this.$store.state.kafkaMonitorListData,
        kafkaMonitorListPagedData: this.$store.state.kafkaMonitorListData,
        charts: '',
        groupListData: [],
        char_address: '',
        char_topic: '',
        char_groupId: '',
        char_logSize: '',
        char_offset: '',
        char_lag: '',
        char_interval: '',
        char_createBy: '',
        char_des: '',
        searchGroupId: '',
        searchCreateBy: '',
        //
        currentPage: 1,
        total: 0,
        pageSize: 20
      }
    },
    mounted() {
      // this.charts = this.char();
      let that = this;
      this.$store.dispatch('getKafkaMonitorList');
      Highcharts.setOptions({
        global: {
          useUTC: false
        },
        lang: {
          months: ['', '', '', '', '', '', '', '', '', '', '', ''],
          weekdays: ['', '', '', '', '', '', '']
        }
      });
      this.charts = Highcharts.chart('spline', {
        chart: {
          type: 'spline',
          marginRight: 10
        },
        title: {
          text: ''
        },
        xAxis: {
          type: 'datetime',
          tickPixelInterval: 150
        },
        yAxis: {
          title: {
            text: ''
          }
        },
        tooltip: {
          /**/
          crosshairs: [true, true],
          formatter: function () {
            return '<b>' + this.series.name + '</b><br/>' +
              '<b>' + 'topic:' + that.char_topic + '</b><br/>' +
              '<b>' + 'group:' + that.char_groupId + '</b><br/>' +
              Highcharts.numberFormat(this.y, 0) + '<br/>' +
              Highcharts.dateFormat('%Y-%m-%d %H:%M:%S', this.x) + '<br/>';
          }
        },
        legend: {
          enabled: false
        },
        series: [{
          name: '',
          data: (function () {
            // 
            let data = [],
              time = (new Date()).getTime(),
              i;
            for (i = -19; i <= 0; i += 1) {
              data.push({
                x: time + i * 3000,
                //y: Math.random()
              });
            }
            return data;
          }()),
          marker: {
            enabled: false
          },
        }]
      });
    },
    methods: {
      tableRowClassName({row, rowIndex}) {
        if (rowIndex === 1) {
          console.info("warning-row");
          return 'warning-row';
        } else if (rowIndex === 3) {
          console.info("success-row");
          return 'success-row';
        }
        return '';
      },
      char(address, topic, groupId) {
        if (!address || !topic || !groupId) {
          return;
        }
        this.char_address = address;
        this.char_topic = topic;
        this.char_groupId = groupId;
        let that = this;
        //
        clearInterval(this.char_interval);
        this.clearChartsData();
        this.char_interval = setInterval(function () {
          getGroupAndKafkaMonitor(address, topic, groupId).then(response => {
            if (response) {
              let data = response.data;
              if (data.code === 200) {
                data = data.data;
                // let offsets = data.group.offsets[0];
                // let modified = offsets.modified;
                let offset = 0;
                let logSize = 0;
                //
                for (i = 0; i < data.group.offsets.length; i++) {
                  offset = offset + data.group.offsets[i].offset;
                  logSize = logSize + data.group.offsets[i].logSize;
                }
                let lag = logSize - offset;
                that.char_logSize = logSize;
                that.char_offset = offset;
                that.char_lag = lag;
                that.char_des = data.kafkaOffsetMonitor.description;
                let x = (new Date()).getTime(), // 
                  y = lag;          // 
                that.charts.series[0].addPoint([x, y], true, true);
              }
            }
          });
          that.activeLastPointToolip(this.charts);
        }, 3000);
        this.$once('hook:beforeDestroy', () => {
          clearInterval(this.char_interval);
        })
      },
      clearChartsData() {
        this.charts.update({
          series: [{
            data: (function () {
              // 
              let data = [],
                time = (new Date()).getTime(),
                i;
              for (i = -19; i <= 0; i += 1) {
                data.push({
                  x: time + i * 1000,
                  //y: Math.random()
                });
              }
              return data;
            }())
          }]
        });
      },
      showAddKafkaMonitorDialog() {
        //
        this.$store.dispatch('getKafkaMonitorManagerList');
        this.$store.state.AddKafkaMonitorDialogShow = true;
      },
      handleEdit(index, row) {
        this.$store.state.kafkaMonitorData = row;
        this.$store.state.UpdateKafkaMonitorDialogShow = true;
      },
      handleDelete(index, row) {
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          //id, address, topic, group, lag, emails, isEmail, isDeleted, desc
          //
          saveKafkaMonitor(row.id, row.address, row.topic, row.groupId, row.lag, null, row.isEmail, true, null).then(response => {
            if (response) {
              let data = response.data;
              if (data.code === 200) {
                this.$message({
                  //,toast
                  showClose: true,
                  message: '',
                  type: 'success'
                });
                //
                this.$store.dispatch('getKafkaMonitorList');
              } else {
                this.$message({
                  //,toast
                  showClose: true,
                  message: ':' + response.data,
                  type: 'error'
                });
              }
            }
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      getGroupAndKafkaMonitorMethod(index, row) {
        this.char_createBy = row.createBy;
        this.char_des = row.description;
        this.char(row.address, row.topic, row.groupId);
      },
      activeLastPointToolip(chart) {
        if (chart) {
          let points = chart.series[0].points;
          chart.tooltip.refresh(points[points.length - 1]);
        }
      },
      //
      handleSizeChange(val) {
        this.pageSize = val;
        this.currentPage = 1;
        this.fetchData(1, val);
        // console.log(` ${val} `);
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.fetchData(val, this.pageSize);
        // console.log(`: ${val}`);
      },
      fetchData(currentPage, pageSize) { //
        //kafkaMonitorListData
        //
        let result = [];
        for (let i = (currentPage - 1) * pageSize; i < currentPage * pageSize; i++) {
          //
          if (i === this.kafkaMonitorListData.length) {
            break;
          }
          result.push(this.kafkaMonitorListData[i]);
        }
        this.kafkaMonitorListPagedData = result;
      },
      //kafkaMonitorListComputed
      kafkaMonitorTableGroupIdFilter() {
        return (state) => {
          return (state.groupId.toLowerCase().includes(this.searchGroupId.toLowerCase()));
        };
      },
      //
      kafkaMonitorTableCreateByFilter() {
        return (state) => {
          return (state.createBy.toLowerCase().includes(this.searchCreateBy.toLowerCase()));
        };
      },
      //
      resetSearchTable() {
        this.searchGroupId = '';
        this.searchCreateBy = '';
      }
    },
    computed: {
      kafkaMonitorListComputed() {
        return this.$store.state.kafkaMonitorListData;
      },
      kafkaMonitorComputed: {
        get() {
          return this.$store.state.kafkaMonitorData
        },
        set(val) {
          this.$store.state.kafkaMonitorData = val;
        }
      }
    },
    watch: {
      searchGroupId() {
        let result = this.searchGroupId ? this.kafkaMonitorListComputed.filter(this.kafkaMonitorTableGroupIdFilter()) : this.kafkaMonitorListComputed;
        this.kafkaMonitorListData = result;
      },
      searchCreateBy() {
        let result = this.searchCreateBy ? this.kafkaMonitorListComputed.filter(this.kafkaMonitorTableCreateByFilter()) : this.kafkaMonitorListComputed;
        this.kafkaMonitorListData = result;
      },
      kafkaMonitorListData() {
        if (this.kafkaMonitorListData) {
          this.currentPage = 1;
          this.total = this.kafkaMonitorListData.length;
          this.kafkaMonitorListPagedData = this.kafkaMonitorListData;
          //
          this.fetchData(this.currentPage, this.pageSize);
        }
      },
      //data
      kafkaMonitorListComputed() {
        this.kafkaMonitorListData = this.$store.state.kafkaMonitorListData;
        this.resetSearchTable();
      }
    }
  }
</script>

<style scoped>
  .el-table .warning-row {
    background: #78fd77;
  }

  .el-table .success-row {
    background: #f0f9eb;
  }

  .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }

  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 100%;
    height: 400px;
  }
</style>



